#SignIncontainer col-xs-6 col-md-4 {
  display: flex;
  font-family: "Roboto";
  width: 100%;
  height: 100%;
  margin: auto;
  justify-content: center;
  align-items: center;
  margin-top: 10%;
}

#SignIn {
  text-align: center;
  background-color: white;
  padding: 2%;
  position: fixed;
  top: 25%;
  width: 30%;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 10px;
  border: 1px solid rgba(100, 100, 111, 0.2);
  overflow: auto;
}

#title {
  font-family: "Roboto";
  font-size: 25px;
  color: #0f0761;
}

#email-signin,
#pass-signin {
  height: 50px;
  margin: 5% 0% 5% 0%;
  border-radius: 10px;
}

.SignInForm {
  padding-top: 20%;
}

#SignIn form {
  width: 100% !important;
}
@media only screen and (max-width: 700px) {
  #SignIn {
    padding-top: 10%;
    width: 100% !important;
    height: 100% !important;
    top: unset !important;
  }
}
